.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 5s linear;
  /*animation: App-logo-spin infinite 20s (...original) linear;*/

  /*height: 40vmin;*/
  height: 114px;

  pointer-events: none;
}

.App-loading {
  background-color: #282c34;

  /*min-height: 100vh;*/
  min-height: 244px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
